.megamenu {
	position: static;
}

.megamenu .dropdown-menu {
	background: none;
	border: none;
}
.dropdown-menu {
	width: 100%;
}
.projects-dropdown {
	width: 80%;
}

.text-uppercase {
	letter-spacing: 0.08em;
}
button.lit-btn {
	background-color: rgb(21, 161, 255);
}
#projects-home {
	margin-right: 100px;
}
#home-logo {
	position: absolute;
	margin-left: 15%;
	border: none;
	transition: 100ms;
	padding: 0px;
	filter: brightness(2);
	top: 8px !important;
	&:hover{
		transform: translateY(-.8px);
	}
}
.custom-shadow{
	box-shadow: 0px 5px 15px rgba(56, 56, 56, 0.1);
}
.nav-color{
	background-color: white !important;
}
