.hljs {
	display: block;
	overflow-x: auto;
	color: #e7e4eb;
}

.hljs-subtr {
	color: #e7e4eb;
}

.hljs-doctag,
.hljs-meta,
.hljs-comment,
.hljs-quote {
	color: #747474;
}

.hljs-selector-tag,
.hljs-selector-id,
.hljs-template-tag,
.hljs-regexp,
.hljs-attr {
	color: #24ff99;
}

.hljs-params,
.hljs-selector-class,
.hljs-bullet {
	color: #f19fff;
}

.hljs-keyword,
.hljs-section,
.hljs-meta-keyword,
.hljs-symbol,
.hljs-type,
.hljs-tag {
	color: #17fc95;
}

.hljs-addition,
.hljs-number,
.hljs-link {
	color: #c5fe00;
}

.hljs-string {
	color: #38c0ff;
}

.hljs-attribute,
.hljs-addition {
	color: #e7ff9f;
}

.hljs-variable,
.hljs-template-variable {
	color: #e447ff;
}

.hljs-builtin-name,
.hljs-built_in,
.hljs-formula,
.hljs-name,
.hljs-title,
.hljs-class,
.hljs-function {
  color: #ff5959;
  /* color: #fff; */
}

.hljs-selector-pseudo,
.hljs-deletion,
.hljs-literal {
  /* color: #ff9e44; */
  color: #fff;
}

.hljs-emphasis,
.hljs-quote {
	font-style: italic;
}

.hljs-params,
.hljs-selector-class,
.hljs-strong,
.hljs-selector-tag,
.hljs-selector-id,
.hljs-template-tag,
.hljs-section,
.hljs-keyword {
	font-weight: bold;
}

